import {
	http
} from "../../utils/ajax.js"
import swiper from '@/components/swiper'
export default {
	data() {
		return {
			title: 'Hello',
			playList: [],
			mvList: [],
			mv: [],
			show: true
		}
	},
	components: {
		swiper
	},
	onLoad() {
		http({
			url: "http://123.57.211.31:8880/personalized",
			method: "GET",
			data: {
				limit: 6
			},
			success: (res) => {
				console.log(res.data.result)
				this.playList = res.data.result
			}
		})
		http({
			url: "http://123.57.211.31:8880/top/mv",
			method: "GET",
			data: {
				limit: 10
			},
			success: (res) => {
				res.data.data.forEach(item => {
					http({
						url: "http://123.57.211.31:8880/mv/detail",
						method: "GET",
						data: {
							mvid: item.id
						},
						success: (res) => {
							var arr = {}
							arr.artistName = res.data.data.artistName
							arr.name = res.data.data.name
							arr.src = res.data.data.brs[240]
							this.mv.push(arr)
						}
					})
				})


			}
		})

	},
	methods: {

	}
}
